export interface TypeIcon {
    value: string,
    label: string
}
export const renderIcon = (dataIcon:any):any => {
    if (dataIcon?.iconName) {
        return <i
          className={dataIcon?.iconName}
          style={{
              fontSize: `${dataIcon?.mf_font_size}`,
              color: `${dataIcon?.mf_color}`,
          }}
        ></i>
    }
    return <></>

}

